import React from "react";
import {Link, useStaticQuery, graphql} from 'gatsby'
import Layout from '../components/Global/Layout/Layout'
import Header from '../components/Global/App/Header/Header.component'
import Footer from '../components/Global/App/Footer/Footer.component'
const PageNotFound = () => {
	const data = useStaticQuery(graphql`
	query
		{
			error: file(relativePath: {eq: "resource/error.png"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
			}
		}`)
	return (
		<Layout>
			<Header />		
			<section className="our-error bgc-f7">
				<div className="container">
					<div className="row">
						<div className="col-lg-10 offset-lg-1 text-center">
							<div className="error_page footer_apps_widget">
								<img className="img-fluid" src={data.error.childImageSharp.fluid.originalImg} alt="error.png"/>
								<div className="erro_code"><h1>Ohh! Page Not Found</h1></div>
								<p>We can’t seem to find the page you’re looking for</p>
								<form className="form-inline mailchimp_form">
									<label className="sr-only" htmlFor="inlineFormInputName">Name</label>
									<input type="text" className="form-control mb-2 mr-sm-2" id="inlineFormInputName" placeholder="Search"/>
									<button type="submit" aria-label="Search"className="btn btn-primary mb-2" ><span className="flaticon-magnifying-glass"></span></button>
								</form>
							</div>
							<Link className="btn btn_error btn-thm" to="/">Back To Home</Link>
						</div>
					</div>
				</div>
			</section>
		<Footer />
	</Layout>

	);
}

export default PageNotFound